  font-weight: 600;
  letter-spacing: 0.1rem;
  text-transform: uppercase;
  color: #ffffff;
  background-color: #3d3bff;
  outline: none;
  cursor: pointer;
  user-select: none;
  transition: background-color 0.3s, border-color 0.3s, color 0.3s;
}
