    margin-bottom: 1.4rem;
  }
}
@media (min-width: 768px) {
  .form__label:last-of-type {
    margin-bottom: 0;
  }
}

.form__input {
  display: block;
  width: 100%;
  padding: 1.7rem 2.8rem 1.3rem;
  border: 2px solid #e6e8f0;
  border-radius: 0;
  font-family: "OpenSans", "Arial", sans-serif;
  line-height: 1.87;
  text-overflow: ellipsis;
  color: #000000;
  background-color: transparent;
  appearance: none;
  outline: none;
  transition: border-color 0.2s;
}
.form__input::placeholder {
  color: transparent;
}
.form__input:focus {
  border-color: #3d3bff;
}
.form__input.invalid {
  border-color: #e71717;
}
.form__input.invalid ~ .form__error {
  visibility: visible;
  opacity: 1;
}

.form__input:focus ~ .form__label-text,
.form__input:not(:placeholder-shown) ~ .form__label-text {
  top: 0.8rem;
  color: #3d3bff;
  transform: scale(0.7);
  transform-origin: 0 0;
}

.form__label-text {
  position: absolute;
  top: 2.4rem;
  left: 2.8rem;
  font-size: 1.6rem;
  line-height: 1;
  color: #657195;
  transition: top 0.3s, color 0.3s, transform 0.3s;
  cursor: text;
}

.form__error {
  position: absolute;
  bottom: 0.3rem;
  left: 0;
  font-size: 1.4rem;
  line-height: 1;
  color: #e71717;
  visibility: hidden;
  opacity: 0;
  transition: opacity 0.3s;
}

