  position: absolute;
  width: 1px;
  height: 1px;
  border: none;
  white-space: nowrap;
  clip: rect(0 0 0 0);
  clip-path: inset(50%);
  overflow: hidden;
  appearance: none;
}
.form__consent-input:checked ~ .form__consent-text::after {
  opacity: 1;
}
.form__consent-input:focus-visible ~ .form__consent-text::before {
  border-color: #3d3bff;
}
.form__consent-input.invalid ~ .form__consent-text::before {
  border-color: #e71717;
}
.form__consent-input.invalid ~ .form__error {
  visibility: visible;
  opacity: 1;
}
.form__consent-input.invalid:focus-visible ~ .form__consent-text::before {
  border-color: #3d3bff;
}
@media (min-width: 768px) {
  .form__consent-input:checked ~ .form__consent-text::before {
    background-color: #3d3bff;
  }
  .form__consent-input:checked ~ .form__consent-text::after {
    opacity: 1;
  }
  .form__consent-input.invalid:focus-visible ~ .form__consent-text::before, .form__consent-input:focus-visible ~ .form__consent-text::before {
    border-color: #e6e8f0;
    box-shadow: 0 0 0 2px #3d3bff;
