  top: 0;
  left: 0;
  width: 2.4rem;
  height: 2.4rem;
  border: 2px solid #e6e8f0;
  background-color: transparent;
  cursor: pointer;
}
.form__consent-text::after {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 2.4rem;
  height: 2.4rem;
  background-repeat: no-repeat;
