.popup {
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  display: none;
  align-items: center;
  justify-content: center;
  opacity: 0;
  transition: opacity 0.3s;
  transition-timing-function: ease;
}
.popup.active {
  display: flex;
  opacity: 1;
  z-index: 1;
}
.popup.active .popup__content {
  transform: scale(1);
  transition-timing-function: ease-out;
}

